import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, LoadingController, AlertController, Platform } from 'ionic-angular';
import { SocialSharing } from '@ionic-native/social-sharing';
import { AppSoundProvider } from '../../providers/app-sound/app-sound';

@IonicPage()
@Component({
    selector: 'page-game-thank-you-submit-tip-modal',
    templateUrl: 'game-Thank-You-SubmitTip-modal.html'
})
export class GameThankyouSubmittip {
    currentGameId: any;
    loading: any;
    constructor(private nvctrl: NavController,
        private navParms: NavParams,
        private viewctrl: ViewController,
        public appSound: AppSoundProvider,
        private loadingctrl: LoadingController,
        public platform: Platform,
        public share: SocialSharing
    ) {
        this.currentGameId = this.navParms.get('current_gameid')
    }

    goToPlayGames() {
        this.appSound.play('buttonClick');
        // this.loading=this.loadingctrl.create();
        // this.loading.present();
        // this.nvctrl.push('PlayGamesPage', { game: this.currentGameId });
        let data = "hello"
        this.viewctrl.dismiss(data);
    }
    shareNow() {
        if (this.platform.is('cordova')) {
            this.share.share("Hello this is message", "This is subject", "", "https://nima.lottosocial.com/").then(() => {

            }).catch((data) => {
                alert(data);
            })
        }
    }
}


